import wasm from '../../rust/Cargo.toml';

export async function getBindings() {
  return await wasm();
}
getBindings().then((bindings) => {
  postMessage({ message: 'ready' });
  let { solve } = bindings;
  window.onmessage = (event) => {
    if (event.data.type === 'start') {
      let solution = solve();
      postMessage({ message: 'solution', solution });
    }
  };
});
